<template>
    <yd-layout class="box_banci">
        <navBar title="班次信息" slot="navbar"></navBar>
        <div class="div_list" v-for="item,index in data" :key="index">
            <div class="list_top">
                <img src="../../assets/shu.png" alt="" class="shu">
                <p class="list_title">班次信息</p>
            </div>
            <div style="margin-bottom: .2rem;">
                <div class="list"><span>名称：</span>{{item.BANCI_NAME}}</div>
                <div class="list"><span>类型：</span>{{item.BANCI_LEIXING}}</div>
                <div class="list"><span>时间：</span>{{item.BANCI_END_TIME}}</div>
            </div>

            <div class="list_top">
                <img src="../../assets/shu.png" alt="" class="shu">
                <p class="list_title">班次人员</p>
            </div>
            <div class="conlist" style="margin-bottom: 0.2rem">
                <span v-for="people,peopleid in item.userlist" :key="peopleid" style="background: #1dc4d9;">{{people.NAME}}</span>
            </div>

            <div class="list_top">
                <img src="../../assets/shu.png" alt="" class="shu">
                <p class="list_title">班次巡检点</p>
            </div>
            <div class="conlist">
                <span v-for="vo,voi in item.xunjiandianlist" :key="voi" style="background: #757ccc;">{{vo.xjdmc}}</span>
            </div>
        </div>
    </yd-layout>
</template>

<script>
    import {api_banci} from "../../components/APIKeys";

    export default {
        name: "banci",
        data: function () {
            return {
                // token:window.localStorage.getItem('token'),
                data:[],//班次信息数据
                name: '日巡检',
                type: '日检',
                time: '01:00:00~23:00:00',
                personnel: [
                    {id: 1, name: '张三'},
                    {id: 2, name: '张三'},
                    {id: 3, name: '张三'},
                    {id: 4, name: '张三'},
                    {id: 5, name: '张三'},
                    {id: 6, name: '张三'},
                    {id: 7, name: '张三'},
                    {id: 8, name: '张三'},
                ],
                patrol: [
                    {id: 1, name: '消防设备'},
                    {id: 2, name: '热压机'},
                    {id: 3, name: '热压机'},
                    {id: 4, name: '热压机'},
                    {id: 5, name: '消防设备'},
                    {id: 6, name: '热压机'},
                    {id: 7, name: '热压机'},
                    {id: 8, name: '消防设备'},
                ]
            }
        },
        mounted(){
          this.doLoadData();
        },
        methods:{
          doLoadData(){
              this.$showDialog();
              let jsonData = {
                  // token:this.token,
              };
              this.$post(api_banci, jsonData).then(res=>{
                  this.$showDialog(false);
                  if(res.errno == 0){
                      this.data = res.data;
                  }else{
                      this.$showError('获取信息失败');
                  }
              })
          }  ,
        },
    }
</script>

<style scoped>
    .div_list {
        margin: .24rem;
        background: #fff;
        padding: .24rem;
        border-radius: 0.1rem;
    }

    .list_top {
        display: flex;
        align-items: center;
        margin-bottom: 0.2rem;
    }

    .shu {
        width: 0.05rem;
        height: 0.3rem;
        margin-right: .2rem;
    }

    .list_title {
        font-size: .32rem;
        font-weight: 600;
    }

    .list {
        font-size: 0.30rem;
        line-height: .5rem;
    }

    .list span {
        color: #999999;
    }

    .conlist {
        display: flex;
        flex-wrap: wrap;
    }

    .conlist span {
        color: #fff;
        text-align: center;
        padding: .1rem;
        margin-right: .1rem;
        border-radius: 5px;
    }
</style>



